*,
*:after,
*:before {
  box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.dg.ac {
  z-index: 99999 !important;
}

.preloader,
.preloader__slider,
.preloader__slide {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.preloader__slide,
.preloader__calendar,
.timer__item-title,
.timer__item-date {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo {
  font-family: "Ricordi";
  font-size: 2rem;
  font-weight: 400;
  line-height: 2.4rem;
}

.timer__item:first-child .timer__item-date:after,
.timer__item:nth-child(2) .timer__item-date:after,
.timer__item-date {
  font-family: "Ricordi";
  font-size: 12.4rem;
  font-weight: 400;
  line-height: 12.4rem;
}

.timer__item-title {
  font-family: "Manrope";
  font-size: 1.2rem;
  font-weight: 400;
  line-height: 1.92rem;
}

.lang-toggle__item {
  font-family: "Manrope";
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 1.68rem;
}

._container {
  box-sizing: content-box;
  margin: 0 auto;
  max-width: 168.8rem;
  width: 100%;
  padding: 0 4.4rem;
}

@font-face {
  font-family: "Ricordi";
  src: url("../../fonts/ricordi-regular.woff2") format("woff2"), url("../../fonts/ricordi-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Manrope";
  src: url("../../fonts/manrope-regular.woff2") format("woff2"), url("../../fonts/manrope-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

html {
  font-size: calc(100vw / 1920 * 10);
}

body {
  font-family: "Manrope";
}

a {
  color: inherit;
  outline: none;
  text-decoration: none;
}

.preloader {
  overflow: hidden;
  background: #9F1307;
}

.preloader__wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}

.preloader__bg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
}

.preloader__slider-wrapper {
  position: relative;
  height: 100%;
}

.preloader__slide {
  height: 100%;
}

.preloader__slide:nth-child(1) {
  opacity: 0;
}

.preloader__calendar {
  width: 56.2rem;
  height: 56.2rem;
}

.preloader__calendar img {
  max-width: 100%;
  max-height: 100%;
}

.timer {
  width: 62.6rem;
}

.timer__wrapper {
  display: flex;
  width: 100%;
}

.timer__item {
  width: 100%;
}

.timer__item:first-child .timer__item-title {
  border-left: 1px solid rgba(255, 255, 255, 0.3);
}

.timer__item:first-child .timer__item-date:after {
  content: ':';
  color: #fff;
  position: absolute;
  right: -8%;
  top: 50%;
  transform: translateY(-50%);
}

.timer__item:nth-child(2) .timer__item-title {
  border-left: 1px solid rgba(255, 255, 255, 0.3);
  border-right: 1px solid rgba(255, 255, 255, 0.3);
}

.timer__item:nth-child(2) .timer__item-date:after {
  content: ':';
  color: #fff;
  position: absolute;
  right: -8%;
  top: 50%;
  transform: translateY(-50%);
}

.timer__item:last-child .timer__item-title {
  border-right: 1px solid rgba(255, 255, 255, 0.3);
}

.timer__item-title {
  height: 3.3rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  color: #fff;
  margin-bottom: 1.8rem;
}

.timer__item-title span {
  margin-bottom: 1.2rem;
}

.timer__item-date {
  position: relative;
  color: #fff;
}

.noise {
  animation: noise 1.2s steps(3) infinite both;
  background: url("../images/noise.png");
  height: 200%;
  left: -50%;
  pointer-events: none;
  position: fixed;
  top: -50%;
  width: 200%;
  will-change: auto;
  z-index: 99998;
  opacity: .1;
}

@keyframes noise {
  0% {
    transform: translate3d(1%, -1%, 0);
  }

  10% {
    transform: translate3d(-5%, -2%, 0);
  }

  20% {
    transform: translate3d(10%, 5%, 0);
  }

  30% {
    transform: translate3d(5%, -11%, 0);
  }

  40% {
    transform: translate3d(-12%, -5%, 0);
  }

  50% {
    transform: translate3d(10%, 9%, 0);
  }

  60% {
    transform: translate3d(15%, 0, 0);
  }

  70% {
    transform: translate3d(-10%, 8%, 0);
  }

  80% {
    transform: translate3d(10%, 2%, 0);
  }

  90% {
    transform: translate3d(1%, 5%, 0);
  }

  100% {
    transform: translate3d(0, 8%, 0);
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  color: #fff;
  height: 8rem;
  display: flex;
  align-items: center;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.logo {
  color: #fff;
  text-transform: uppercase;
  white-space: nowrap;
  letter-spacing: 0.02rem;
}

.burger {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.burger div {
  width: 5.6rem;
  background: #fff;
}

.burger div:first-child {
  margin-bottom: 5px;
  height: 1px;
}

.burger div:last-child {
  height: 3px;
}

.lang-toggle {
  border-left: 3px solid #fff;
  padding-left: .6rem;
  display: flex;
}

.lang-toggle__item {
  color: #fff;
  opacity: 0.4;
}

.lang-toggle__item._active {
  opacity: 1;
}

.lang-toggle__item:not(:last-child) {
  margin-right: 1.2rem;
}

@media (min-width: 769px) {
  .preloader__bg {
    width: 100%;
  }

  .header__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

@media (min-width: 1920px) {
  .logo {
    font-size: 2.8rem;
  }

  .timer__item:first-child .timer__item-date:after,
  .timer__item:nth-child(2) .timer__item-date:after,
  .timer__item-date {
    font-size: 16.4rem;
    line-height: 16.4rem;
  }

  .preloader__calendar {
    width: 86.8rem;
    height: 86.8rem;
  }

  .timer {
    width: 72.1rem;
  }
}

@media (min-width: 1920px) and (max-height: 1040px) {
  .preloader__calendar {
    width: 76.8rem;
    height: 76.8rem;
  }
}

@media (max-width: 1280px) {
  ._container {
    max-width: 111.2rem;
  }

  html {
    font-size: calc(100vw / 1280 * 10);
  }

  .header {
    height: 7.2rem;
  }
}

@media (max-width: 768px) {
  .logo {
    font-size: 2.4rem;
  }

  ._container {
    max-width: 68rem;
  }

  html {
    font-size: calc(100vw / 768 * 10);
  }

  .preloader__bg {
    width: 100% !important;
    height: 100%;
    max-width: none;
    box-sizing: border-box;
    margin: 0;
    padding: 0 !important;
  }

  .preloader__calendar {
    width: 71.4rem;
    height: 71.4rem;
  }

  .header {
    height: 6.8rem;
  }

  .header__lang {
    display: none;
  }

  .burger {
    width: 4.8rem;
    height: 4.8rem;
    background: #fff;
    border-radius: 100%;
  }

  .burger div {
    background: #000;
    width: 3rem;
  }

  .burger div:first-child {
    margin-bottom: 6px;
  }

  .burger div:last-child {
    height: 2px;
  }
}

@media (max-width: 768px) and (max-height: 850px) {
  .preloader__calendar {
    width: 51.4rem;
    height: 51.4rem;
  }
}

@media (max-width: 768px) and (max-height: 650px) {
  .preloader__calendar {
    width: 31.4rem;
    height: 31.4rem;
  }
}

@media (max-width: 375px) {
  .logo {
    font-size: 1.6rem;
  }

  .timer__item:first-child .timer__item-date:after,
  .timer__item:nth-child(2) .timer__item-date:after,
  .timer__item-date {
    font-size: 9.2rem;
    line-height: 9.2rem;
  }

  ._container {
    padding: 0 16px;
    width: calc(100% - 32px);
  }

  html {
    font-size: calc(100vw / 375 * 10);
  }

  .preloader__slider {
    padding: 0 16px;
  }

  .preloader__calendar {
    width: 100%;
  }

  .timer {
    width: 14.3rem;
  }

  .timer__wrapper {
    flex-direction: column;
  }

  .timer__item {
    margin-bottom: 3.2rem;
  }

  .timer__item:first-child .timer__item-title {
    border-right: 1px solid rgba(255, 255, 255, 0.3);
  }

  .timer__item:last-child .timer__item-title {
    border-left: 1px solid rgba(255, 255, 255, 0.3);
  }

  .timer__item-title {
    margin-bottom: 0;
  }

  .timer__item-date:after {
    display: none;
  }

  .header {
    height: 4.8rem;
  }

  .burger {
    width: 3.2rem;
    height: 3.2rem;
    background: #fff;
    border-radius: 100%;
  }

  .burger div {
    width: 2rem;
  }

  .burger div:first-child {
    margin-bottom: 3px;
  }
}

@media (min-width: 769px) and (max-width: 1919px) and (max-height: 700px) {
  .preloader__calendar {
    width: 40.8rem;
    height: 40.8rem;
  }
}

@media (min-width: 769px) and (max-width: 1919px) and (max-height: 560px) {
  .preloader__calendar {
    width: 30.8rem;
    height: 30.8rem;
  }
}